* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  width: 50%;
  background-color: #080808;
  border-radius: 5px;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 20px;
  display: flex;
  box-shadow: 1px 1px 5px rgba(255, 255, 255, .15), 0 15px 90px 30px rgba(0, 0, 0, .25);
}

.field {
  text-align: center;
  min-width: 90px;
  flex-direction: column;
  margin-left: 40px;
  margin-right: 40px;
  display: flex;
  position: relative;
}

.field:not(:last-child):before, .field:not(:last-child):after {
  content: "";
  height: 5px;
  width: 5px;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 50%;
  display: block;
  position: absolute;
  right: -42px;
}

.field:not(:last-child):before {
  top: 35%;
}

.field:not(:last-child):after {
  top: 50%;
}

.value {
  color: #fff;
  text-transform: uppercase;
  font-size: 4.2rem;
}

.label {
  color: rgba(255, 255, 255, .35);
  text-transform: uppercase;
  margin-top: 10px;
  font-size: .7rem;
}

